.row-block {
  display: flex;
  justify-content: flex-start;
  div {
    padding: 4px;
  }
}
.row-block__header {
  position: relative;
  font-weight: 600;
  background-color: rgb(145, 157, 169);
  padding: 4px;
  min-width: 54px;
  // border-right: 1px solid grey;
  max-width: 620px;
  .resable {
    position: absolute;
    top: 0;
    right: 0px;
    bottom: 0;
    width: 2px;
    cursor: col-resize;
    // background-color: red;
    z-index: 3;
    &:hover {
      background-color: royalblue;
    }
  }
}
.row-block__body {
  padding: 4px;
  // border-right: 1px solid grey;
  text-align: center;
  background-color: #e2e0a8;
  input {
    border: 1px solid transparent;
    background-color: #f5f1f16b;
    padding: 6px;
    &:hover {
      background-color: #fff;
    }
    &:active {
      background-color: #fff;
    }
    &:focus {
      background-color: #fff;
    }
  }
  select {
    min-width: 40px;
    padding: 4px;
    border: 1px solid grey;
    background-color: beige;
  }

  tr.odds {
    background-color: rgb(223, 223, 223);
    height: 20px;
    // border-bottom: 1px solid grey;
  }
  
  tr.even {
    background-color: rgb(233, 233, 233);
    height: 20px;
    // border-bottom: 1px solid grey;
  }
  .editing-div {
    padding: 8px;
    background-color: beige;
    &:focus-visible {
      // border: 1px solid transparent;
      
    }

    &[contenteditable] {
      outline: 0px solid transparent;
      border: none;
      z-index: 2;
      &:focus-visible {
        
        outline: 2px solid #3c74ff;
      }
    }
  }

}
