.des-adv-tools-block {
  display: flex;
  justify-content: space-between;
  border: 1px solid rgba(128, 128, 128, 0.153);
  margin-top: 6px;
  padding: 4px;
  border-radius: 4px;
  margin-bottom: 4px;
  div {
    display: flex;
  }
  .line-block {
    box-shadow: 1px 1px 1px 1px rgba(128, 128, 128, 0.194);
    margin: 4px;
    &:hover {
      box-shadow: 1px 1px 1px 1px rgba(128, 128, 128, 0);
    }
    select {
      min-width: 76px;
    }
  }
}