$app-background-color: rgb(170, 26, 26);
$app-font-size: 14px;
$app-padding: 4px;
$color-p: #162e87;
$color-a: #4da0cf;
 

img {
  // width: 100%;
  border-style: none;
  border-width: 0;
}


.bold {
  color: #000000;
  font-family: Verdana, Geneva, Arial, Helvetica, sans-serif;
  font-weight: bold;
}

 
#remember {
  margin-inline: 4px;

}




.contact-block_header {
  text-align: center;
  font-size: large;
  font-weight: bold;
  color: $color-p;
  padding: 4px 0;

}

.contact-block {
  margin-top: 10px;
  border: 1px solid #9fd5d0;
  .contact-block_content {
    display: flex;
    flex-direction: column;
    margin-top: 4px;
    margin-bottom: 4px;
    border-radius: 4px;
    padding: 4px;
    .contact-block_content_title {
      text-align: left;
      padding: $app-padding;
      font-size: $app-font-size;
      line-height: 1.3;
      border-bottom: 1px dotted gainsboro;
    }
    .contact-block_content_body {
      display: flex;
      justify-content: left;
      gap: 12px;
      margin-top: 4px;
      a {
        color: $color-a;
      }
    }
    
  }
  

}




.contact-block_content_phone {
  display: flex;
  gap: 4px;
}

.contact-block_content_phone i {
  font-size: small;
}
.contact-block_content_phone a {
  font-size: small;
}

.contact-block_content_email {
  display: flex;
  gap: 4px;
}

.img-certificat {
  margin: 16px auto;
  text-align: center;
}

.copyright-block {
  display: flex;
  justify-content: center;
  // font-size: $app-font-size;
  padding: 10px;
  align-items: center;
  .copyright-a {
    white-space: nowrap;
  }
  div {
    margin: 2px 4px;
    a {
      color: $color-a;
    }
  }
}

.img-pic {
  width: 198px;
  height: 98px;
}

.baner { display: flex; justify-content: center }
.info-panel-footer { 
  display: flex; 
  justify-content: center;
  // border: 1px solid grey;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 0px 2px #e2e0a8;
  img {
    height: 128px;
    width: 46px;
  }
  p{
    font-size: $app-font-size;
    color: #404866;
    line-height: 1.4;
  }

}
// bootstrap
.text-muted {
  color: #6c757d!important;
}
.form-text {
  display: block;
  margin-top: 0.25rem;
}
.small, small {
  font-size: 80%;
  font-weight: 400;
}
.form-check {
  position: relative;
  display: block;
  padding-left: 1.25rem;
}
.form-group {
  margin-bottom: 1rem;
}
input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

.form-check-input {
  position: absolute;
  margin-top: 2px;
  margin-left: -1.25rem;
}
.form-check-label {
  margin-bottom: 0;
  a {
    color: $color-a;
    padding: 0 8px;
  }
}
.form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

/*
* button
*/
.sub {
  margin-top: 4px;
}
.btn-primary {
  color: #fff;
  background-color: #4099cb;
  border-color: #2c5078;
}

.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.25rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

#aspnetForm {
  // border: 1px solid grey;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 0px 2px #e2e0a8;
  p{
    font-size: $app-font-size;
    color: $color-p;
    line-height: 1.5;
  }
  .main {
    .input-wrap {
      padding: 1.5rem;
      margin-right: 0;
      margin-left: 0;
      border-width: 0.2rem;
    }
    img {
      text-align: center;
    }
    .info-block {
      font-size: $app-font-size;
      flex-direction: column;
      li {
        text-align: left;
        padding-left: 8px;
        color: $color-p;
      }
      p{
        font-size: $app-font-size;
        color: $color-p;
        line-height: 1.5;
      }
    }
    
  }
}

