.modal {
  z-index: 1072;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  outline: 0;
}

.bd-modal {
  background-color: #fafafa;
  .modal {
    position: relative;
    top: 40%;
    right: auto;
    bottom: auto;
    left: 50%;
    z-index: 1;
    display: block;
    width: 600px;
    transform: translate(-50%, -50%);
    .modal-dialog {
      left: auto;
      margin-right: auto;
      margin-left: auto;
      .modal-content {
        position: relative;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column;
        width: 100%;
        pointer-events: auto;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid rgba(0,0,0,.2);
        border-radius: 0.3rem;
        outline: 0;
        .modal-header {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: start;
          -ms-flex-align: start;
          align-items: flex-start;
          -webkit-box-pack: justify;
          -ms-flex-pack: justify;
          justify-content: space-between;
          padding: 1rem;
          border-bottom: 1px solid #e9ecef;
          border-top-left-radius: 0.3rem;
          border-top-right-radius: 0.3rem;
          .modal-title {
            padding: 0;
            margin: 0;
          }
        }
        .modal-body {
          position: relative;
          -webkit-box-flex: 1;
          -ms-flex: 1 1 auto;
          flex: 1 1 auto;
          padding: 1rem;
        }
        .modal-footer {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
          -ms-flex-align: center;
          align-items: center;
          -webkit-box-pack: end;
          -ms-flex-pack: end;
          justify-content: flex-end;
          padding: 1rem;
          border-top: 1px solid #e9ecef;
          .btn {
            padding: 4px 6px !important;
            margin-right: 6px;
          }
        }

      }
    }
  }
  
}

.modal-dialog {
  position: relative;
  width: auto;
  margin: 0.5rem;
  pointer-events: none;
}

.bd-des {
  position: absolute;
    top: 0;
    left: 0;
    /* transform: translate(-50%, -50%); */
    padding: 1rem;
    margin: 1rem -15px 0;
    border: solid #f7f7f9;
    border-width: 0.2rem 0 0;
    bottom: 0;
    right: 0;
    background-color: #000000ab;
}


 
@media only screen and (max-width: 2400px) and (min-width: 768px) {
  .bd-des {
    padding: 1.5rem;
    margin-right: 0;
    margin-left: 0;
    border-width: 0.2rem;
  }
  .modal-dialog {
    max-width: 500px;
    margin: 1.75rem auto;
}
}
