.line-block {
  display: flex;
  justify-content: flex-start;
  font-size: larger;
  div {
    padding: 4px;
    min-width: 46px;
    line-height: 22px;
  }
  button {
    padding: 4px;
    min-width: 46px;
    background-color: burlywood;
  }
  .bold {
    font-weight: 600;
    min-width: 86px;
    font-size: small;
  }
  input {
    font-size: initial;
    font-style: normal;
    font-family: 'Roboto';
    padding: 1px;
    text-align: center;
    border: 1px solid #d3d3d300;
    background-color: #faebd759; 
  }
}